<template>
  <input
    :id="`fileQuestion${question.order}Value`"
    :name="`question[${question.order}][value]`"
    type="file"
    class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
    accept="image/*"
    @input="fileSelected"
  />

  <div v-if="imageSrc" key="isDescriptionHasImage">
    <img
      :src="imageSrc"
      :title="$t('labels.uploaded_image')"
      :alt="$t('labels.description_image')"
      class="max-w-full max-h-[500px]"
    />
  </div>

  <question-options
    v-if="showOptions"
    key="isQuestionOptionsShowAble"
    :template="template"
    :question="question"
    :required="false"
    :score="false"
    :store-as-type="false"
  ></question-options>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import QuestionOptions from "@/components/QuestionInputs/QuestionOptions.vue";
import Toast from "@/core/Toast";

export default {
  name: "OutputDescription",
  components: { QuestionOptions },
  props: {
    template: {
      type: Template,
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
    showOptions: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      imageSrc: null,
    };
  },
  methods: {
    fileSelected(event) {
      const file = event.target.files[0];
      if (file) {
        if (!file.type.startsWith("image/")) {
          Toast.fire(this.$t("errors.upload.file_must_be_image"), "error");

          return;
        }

        const reader = new FileReader();
        reader.onload = (e) => {
          this.imageSrc = e.target.result;
        };
        reader.readAsDataURL(file);

        this.question.setValue(file);
      }
    },
  },
  mounted() {
    this.imageSrc = this.question.getValueImage();
  },
};
</script>
