<template>
  <div class="flex flex-col gap-3">
    <draggable
      :list="question.values"
      :item-key="(item, index) => index"
      :tag="listTagType"
      v-bind="dragOptions"
      handle=".handle"
      class="space-y-4 ps-6"
      @start="drag = true"
      @end="drag = false"
      :class="`list-${list_style}`"
    >
      <template #item="{ element, index }">
        <li>
          <div class="flex items-center gap-2">
            <input
              :name="`questions[${question.order}][values][${index}][label]`"
              class="w-full border-b border-slate-300 bg-transparent pb-1 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              type="text"
              :value="element.label"
              @input="question.setValues(index, 'label', $event.target.value)"
            />

            <div class="flex flex-row">
              <div
                class="cursor-move text-slate-400 transition-colors handle hover:text-slate-800"
              >
                <i class="fa fa-fw fa-align-justify"></i>
              </div>

              <button
                type="button"
                class="text-slate-400 transition-colors close hover:text-slate-800"
                @click="question.removeValues(index)"
              >
                <i class="fa fa-fw fa-times"></i>
              </button>
            </div>
          </div>
        </li>
      </template>
    </draggable>

    <div>
      <button
        type="button"
        class="w-full text-end text-slate-400 transition-colors close hover:text-slate-800"
        @click="question.addValues($t('labels.option'))"
      >
        <span class="mr-1">{{ $t("labels.add_item") }}</span>
        <i class="fa fa-fw fa-plus"></i>
      </button>
    </div>
  </div>

  <question-options
    v-if="showOptions"
    key="isQuestionOptionsShowAble"
    :required="false"
    :template="template"
    :question="question"
    :score="false"
  >
    <div class="grid grid-cols-12 items-center gap-3 gap-y-1">
      <div class="col-span-1 text-end">
        <label
          :for="`selectQuestion${question.order}ListStyle`"
          class="font-medium text-slate-600 mb-1.5 dark:text-navy-100"
          >{{ $t("attributes.list_style") }}</label
        >
      </div>
      <div class="col-span-11">
        <select
          v-model="list_style"
          :id="`selectQuestion${question.order}ListStyle`"
          class="form-select h-8 w-full rounded-lg border border-slate-300 bg-white px-2 py-1 text-xs+ hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:bg-navy-700 dark:hover:border-navy-400 dark:focus:border-accent"
          @input="question.setOption('list_style', $event.target.value)"
        >
          <option
            v-for="(style, index) in list_styles"
            :key="index"
            :value="style"
          >
            {{ $t(`options.list_styles.${style}`) }}
          </option>
        </select>
      </div>
    </div>
  </question-options>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import QuestionOptions from "@/components/QuestionInputs/QuestionOptions.vue";
import draggable from "vuedraggable";

export default {
  name: "OutputList",
  components: { QuestionOptions, draggable },
  props: {
    template: {
      type: Template,
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
    showOptions: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      drag: false,
      list_style: "disc",
      list_styles: [
        "none",
        "disc",
        "circle",
        "square",
        "disclosure-closed",
        "disclosure-open",
        "decimal",
        "decimal-leading-zero",
        "upper-latin",
        "lower-latin",
        "upper-roman",
        "lower-roman",
      ],
    };
  },
  created() {
    this.list_style = this.question.getOption("list_style", "disc");
  },
  computed: {
    listTagType() {
      if (
        [
          "decimal",
          "decimal-leading-zero",
          "upper-latin",
          "lower-latin",
          "upper-roman",
          "lower-roman",
        ].includes(this.list_style)
      ) {
        return "ol";
      }

      return "ul";
    },
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>
