<template>
  <div
    class="rounded border border-slate-200 p-4 space-y-3 dark:border-navy-600"
  >
    <div
      v-if="required"
      key="isQuestionNeedsDescription"
      class="grid grid-cols-12 items-center gap-3"
    >
      <div class="col-span-1 text-end">
        <label
          :for="`checkboxQuestion${question.order}Required`"
          class="font-medium text-slate-600 mb-1.5 dark:text-navy-100"
          >{{ $t("attributes.required") }}</label
        >
      </div>
      <div class="col-span-11">
        <label class="inline-flex items-center space-x-2">
          <input
            :id="`checkboxQuestion${question.order}Required`"
            :name="`questions[${question.order}][required]`"
            class="rounded border-slate-400/70 form-checkbox is-basic h-3.5 w-3.5 checked:border-slate-500 checked:bg-slate-500 hover:border-slate-500 focus:border-slate-500 dark:border-navy-400 dark:checked:bg-navy-400"
            type="checkbox"
            :checked="question.required"
            @input="question.setRequired(!question.required)"
          />
        </label>
      </div>
    </div>

    <div class="grid grid-cols-12 items-center gap-3">
      <div class="col-span-1 text-end">
        <label
          :for="`numberQuestion${question.order}Order`"
          class="font-medium text-slate-600 mb-1.5 dark:text-navy-100"
          >{{ $t("attributes.order") }}</label
        >
      </div>
      <div class="col-span-2">
        <input
          :id="`numberQuestion${question.order}Order`"
          type="number"
          disabled
          class="h-8 w-full rounded-lg border border-slate-300 bg-transparent px-2 py-1 text-xs+ placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary disabled:pointer-events-none disabled:select-none disabled:bg-zinc-100 dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent dark:disabled:bg-navy-600"
          :value="question.order"
        />
      </div>
    </div>

    <div
      v-if="score"
      key="isQuestionNeedsDescription"
      class="grid grid-cols-12 items-center gap-3"
    >
      <div class="col-span-1 text-end">
        <label
          :for="`numberQuestion${question.order}Score`"
          class="font-medium text-slate-600 mb-1.5 dark:text-navy-100"
          >{{ $t("attributes.score") }}</label
        >
      </div>
      <div class="col-span-2">
        <input
          :id="`numberQuestion${question.order}Score`"
          :name="`questions[${question.order}][score]`"
          type="number"
          class="form-input h-8 w-full rounded-lg border border-slate-300 bg-transparent px-2 py-1 text-xs+ placeholder:text-slate-400/70 hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
          :value="question.score"
          @input="question.setScore($event.target.value)"
        />
      </div>
    </div>

    <div
      v-if="description"
      key="isQuestionNeedsDescription"
      class="grid grid-cols-12 items-center gap-3 gap-y-1"
    >
      <div class="col-span-1 text-end">
        <label
          :for="`textQuestion${question.order}Description`"
          class="font-medium text-slate-600 mb-1.5 dark:text-navy-100"
          >{{ $t("attributes.description") }}</label
        >
      </div>
      <div class="col-span-11">
        <input
          :id="`textQuestion${question.order}Description`"
          :name="`questions[${question.order}][description]`"
          class="form-input h-8 w-full rounded-lg border border-slate-300 bg-transparent px-2 py-1 text-xs+ placeholder:text-slate-400/70 hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
          type="text"
          :value="question.description"
          @input="question.setDescription($event.target.value)"
        />
      </div>
      <div class="col-span-11 col-start-2">
        <small>{{ $t("descriptions.question_description") }}</small>
      </div>
    </div>

    <slot></slot>

    <div
      v-if="storeAsType"
      key="isQuestionStoreAbleAsType"
      class="grid grid-cols-12 items-center gap-3"
    >
      <div class="col-start-2 col-end-13">
        <v-button color="info" @click="$refs.modal.toggle()"
          >{{ $t("modal.titles.save_question") }}
        </v-button>
      </div>
    </div>
  </div>

  <v-modal v-if="storeAsType" key="isQuestionStoreAbleAsType" ref="modal">
    <template #title>{{ $t("modal.titles.save_question") }}</template>
    <template #body>
      <div class="text-start space-y-3">
        <div>
          <label
            :for="`textQuestionType${question.order}Label`"
            class="font-medium"
            >{{ $t("attributes.question_name") }}</label
          >
          <input
            :id="`textQuestionType${question.order}Label`"
            class="form-input mt-2 w-full rounded-lg border border-slate-300 bg-transparent px-2 h-9 text-xs+ placeholder:text-slate-400/70 hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
            type="text"
            v-model="question_type.label"
          />
        </div>

        <div>
          <label
            :for="`textQuestionType${question.order}Description`"
            class="font-medium"
            >{{ $t("attributes.question_description") }}</label
          >
          <input
            :id="`textQuestionType${question.order}Description`"
            class="form-input mt-2 w-full rounded-lg border border-slate-300 bg-transparent px-2 h-9 text-xs+ placeholder:text-slate-400/70 hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
            type="text"
            v-model="question_type.description"
          />
        </div>

        <div v-if="question_type.isUpdatable" key="isQuestionTypeCustom">
          <small>{{ $t("descriptions.save_as_for_new_question_type") }}</small>
        </div>
      </div>
    </template>
    <template #buttons>
      <div
        v-if="question_type.isUpdatable"
        key="isQuestionTypeCustom"
        class="space-x-3"
      >
        <v-button color="info" @click="storeType"
          >{{ $t("labels.save_as") }}
        </v-button>
        <v-button color="success" @click="updateType"
          >{{ $t("labels.submit") }}
        </v-button>
      </div>
      <div v-else>
        <v-button color="success" @click="storeType"
          >{{ $t("labels.submit") }}
        </v-button>
      </div>
    </template>
  </v-modal>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import ModelRequest from "@/request/ModelRequest";
import { clone } from "lodash";

export default {
  name: "QuestionOptions",
  props: {
    template: {
      type: Template,
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
    required: {
      type: Boolean,
      default: true,
    },
    description: {
      type: Boolean,
      default: true,
    },
    score: {
      type: Boolean,
      default: true,
    },
    storeAsType: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      question_type: null,
      request: new ModelRequest(),
    };
  },
  created() {
    this.question_type = clone(this.question.questionType);

    this.request.bind(this.question_type);
  },
  methods: {
    updateQuestionType() {
      const questionType = this.question.questionType;

      this.question_type.saveAttributes({
        main_question_type_id:
          questionType.main_question_type_id ?? questionType.id,
        component: questionType.component,
        type: questionType.type,
        required: this.question.required,
        value: this.question.value,
        score: this.question.score,
        values: this.question.values,
        options: this.question.options,
      });
    },
    storeType() {
      this.updateQuestionType();

      this.request.store().then(() => {
        this.question_type.getOwnCollection().fetch();
      });
    },
    updateType() {
      this.updateQuestionType();

      this.request.update();
    },
  },
};
</script>
