<template>
  <textarea
    :id="`textareaQuestion[${question.order}]Value`"
    :name="`questions[${question.order}][value]`"
    class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-textarea form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
    rows="5"
    :placeholder="$t('placeholder.output_description')"
    :value="question.value"
    @input="question.setValue($event.target.value)"
  />

  <div v-if="imageSrc" key="isDescriptionHasImage">
    <img
      :src="imageSrc"
      :title="$t('labels.uploaded_image')"
      :alt="$t('labels.description_image')"
      class="max-w-full max-h-[500px]"
    />
  </div>

  <question-options
    v-if="showOptions"
    key="isQuestionOptionsShowAble"
    :template="template"
    :question="question"
    :required="false"
    :description="false"
    :score="false"
    :store-as-type="false"
  >
    <div class="grid grid-cols-12 items-center gap-3">
      <div class="col-span-1 text-end">
        <label
          :for="`selectQuestion${question.order}Image`"
          class="font-medium text-slate-600 mb-1.5 dark:text-navy-100"
          >{{ $t("attributes.image") }}</label
        >
      </div>
      <div class="col-span-11">
        <input
          :id="`selectQuestion${question.order}Image`"
          :name="`questions[${question.order}][options][image]`"
          type="file"
          class="w-full rounded-lg border border-slate-300 bg-transparent px-2 py-1 text-xs+ placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
          accept="image/*"
          @input="fileSelected"
        />
      </div>
    </div>

    <div class="grid grid-cols-12 items-center gap-3">
      <div class="col-span-1 text-end">
        <label
          :for="`selectQuestion${question.order}ImagePosition`"
          class="font-medium text-slate-600 mb-1.5 dark:text-navy-100"
          >{{ $t("attributes.image_position") }}</label
        >
      </div>
      <div class="col-span-11">
        <select
          :id="`selectQuestion${question.order}ImagePosition`"
          :name="`questions[${question.order}][options][image_position]`"
          class="form-select h-8 w-full rounded-lg border border-slate-300 bg-white px-2 py-1 text-xs+ hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:bg-navy-700 dark:hover:border-navy-400 dark:focus:border-accent"
          @input="question.setOption('image_position', $event.target.value)"
        >
          <option
            v-for="(position, index) in image_positions"
            :key="index"
            :value="position"
          >
            {{ $t(`options.image_positions.${position}`) }}
          </option>
        </select>
      </div>
    </div>
  </question-options>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import QuestionOptions from "@/components/QuestionInputs/QuestionOptions.vue";
import Toast from "@/core/Toast";

export default {
  name: "OutputDescription",
  components: { QuestionOptions },
  props: {
    template: {
      type: Template,
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
    showOptions: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      imageSrc: null,
      image_positions: ["top", "right", "bottom", "left"],
    };
  },
  methods: {
    fileSelected(event) {
      const file = event.target.files[0];
      if (file) {
        if (!file.type.startsWith("image/")) {
          Toast.fire(this.$t("errors.upload.file_must_be_image"), "error");

          return;
        }

        const reader = new FileReader();
        reader.onload = (e) => {
          this.imageSrc = e.target.result;
        };
        reader.readAsDataURL(file);

        this.question.setOption("image", file);
      }
    },
  },
  mounted() {
    this.imageSrc = this.question.getOptionImage();
  },
};
</script>
