<template>
  <div>
    <input
      :id="`textQuestion${question.order}OptionsTitle`"
      :name="`questions[${question.order}][options][title]`"
      class="w-full border-b border-slate-300 bg-transparent pb-2 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
      :placeholder="$t('placeholder.type_multiple_question_title')"
      type="text"
      :value="question.getOption('title')"
      @input="question.setOption('title', $event.target.value)"
    />
  </div>

  <div class="grid grid-cols-8 items-start gap-5">
    <div
      class="col-span-full flex flex-col gap-3 rounded border border-slate-200 p-4 2xl:col-span-5"
    >
      <div
        v-for="quest in template.getRelatedQuestions(question)"
        :key="quest.order"
        class="flex items-center gap-2"
      >
        <div>
          <label class="inline-flex items-center space-x-2">
            <input
              :id="`checkboxQuestion${quest.order}Required`"
              :name="`questions[${quest.order}][required]`"
              class="rounded border-slate-400/70 form-checkbox is-basic h-3.5 w-3.5 checked:border-slate-500 checked:bg-slate-500 hover:border-slate-500 focus:border-slate-500 dark:border-navy-400 dark:checked:bg-navy-400"
              type="checkbox"
              :title="$t('attributes.required')"
              :checked="quest.required"
              @input="quest.setRequired(!quest.required)"
            />
          </label>
        </div>

        <span
          v-if="question.question_order"
          key="isQuestionTypeInput"
          class="shrink-0 border-b border-slate-300 pb-1"
          >{{
            $t("labels.question_order", { order: quest.question_order })
          }}</span
        >

        <input
          :id="`textQuestion${quest.order}Label`"
          :name="`questions[${quest.order}][label]`"
          class="w-full border-b border-slate-300 bg-transparent pb-1 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
          type="text"
          :placeholder="$t('attributes.question')"
          :value="quest.label"
          @input="quest.setLabel($event.target.value)"
        />

        <div v-if="quest.order !== question.order" key="isNotMainRelatedQuest">
          <button
            type="button"
            class="text-slate-400 transition-colors close hover:text-slate-800"
            @click="template.removeQuestion(quest)"
          >
            <i class="fa fa-fw fa-times"></i>
          </button>
        </div>
      </div>

      <div>
        <button
          type="button"
          class="w-full text-end text-slate-400 transition-colors close hover:text-slate-800"
          @click="template.addRelatedQuestion(question)"
        >
          <span class="mr-1">{{ $t("labels.add_question") }}</span>
          <i class="fa fa-fw fa-plus"></i>
        </button>
      </div>
    </div>

    <div
      class="col-span-full flex flex-col gap-3 rounded border border-slate-200 p-4 2xl:col-span-3"
    >
      <draggable
        :list="question.values"
        :item-key="(item, index) => index"
        :component-data="{
          tag: 'div',
          type: 'transition-group',
          name: !drag ? 'flip-list' : null,
        }"
        v-bind="dragOptions"
        handle=".handle"
        class="space-y-4"
        @start="drag = true"
        @end="drag = false"
      >
        <template #item="{ element, index }">
          <div class="flex items-center gap-2">
            <label class="inline-flex">
              <input
                :id="`radioQuestion${question.order}Values${index}`"
                class="h-4 w-4 rounded-full border-slate-400/70 form-radio is-basic checked:border-slate-500 checked:bg-slate-500 hover:border-slate-500 focus:border-slate-500 dark:border-navy-400 dark:checked:bg-navy-400"
                type="radio"
              />
            </label>

            <input
              :id="`textQuestion${question.order}Values${index}Label`"
              :name="`questions[${question.order}][values][${index}][label]`"
              class="w-full border-b border-slate-300 bg-transparent pb-1 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              type="text"
              :value="element.label"
              @input="question.setValues(index, 'label', $event.target.value)"
            />

            <input
              :id="`numberQuestion${question.order}Values${index}Score`"
              :name="`questions[${question.order}][values][${index}][score]`"
              class="w-20 border-b border-slate-300 bg-transparent pb-1 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              type="number"
              :value="element.score"
              :placeholder="$t('attributes.score')"
              @input="question.setValues(index, 'score', $event.target.value)"
            />

            <div class="flex flex-row">
              <div
                class="cursor-move text-slate-400 transition-colors handle hover:text-slate-800"
              >
                <i class="fa fa-fw fa-align-justify"></i>
              </div>

              <button
                type="button"
                class="text-slate-400 transition-colors close hover:text-slate-800"
                @click="question.removeValues(index)"
              >
                <i class="fa fa-fw fa-times"></i>
              </button>
            </div>
          </div>
        </template>
      </draggable>

      <div>
        <button
          type="button"
          class="w-full text-end text-slate-400 transition-colors close hover:text-slate-800"
          @click="question.addValues($t('labels.option'))"
        >
          <span class="mr-1">{{ $t("labels.add_answer") }}</span>
          <i class="fa fa-fw fa-plus"></i>
        </button>
      </div>
    </div>
  </div>

  <question-options
    v-if="showOptions"
    key="isQuestionOptionsShowAble"
    :template="template"
    :question="question"
    :score="false"
    :required="false"
    :store-as-type="false"
  ></question-options>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import QuestionOptions from "@/components/QuestionInputs/QuestionOptions.vue";
import draggable from "vuedraggable";

export default {
  name: "InputMultipleRadioGroup",
  components: { QuestionOptions, draggable },
  props: {
    template: {
      type: Template,
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
    showOptions: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      drag: false,
    };
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>
